.social {
  display: block;
  list-style: none;
  margin: 12px 0;
  padding: 0;
}
.social::before, .social::after {
  content: "";
  display: table;
  clear: both;
}
.social li {
  display: inline-block;
  margin-right: 8px;
}
.social li:last-child {
  margin-right: 0;
}
.social svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: #fff;
}

a svg {
  transition: ease-in-out 0.1s all;
}
a:hover svg {
  transform: scale(1.2, 1.2);
}

.meetup-link {
  color: #F64060;
}
.meetup-link svg {
  fill: #F64060;
  margin-right: 0;
}

.slack-link svg {
  fill: #0da1b8;
  margin-right: 0;
}

h2 .meetup-link svg,
h2 .slack-link svg {
  width: 24px;
  height: 24px;
}

h3 .meetup-link svg,
h3 .slack-link svg {
  width: 18px;
  height: 18px;
  margin-bottom: -2px;
}

p .meetup-link svg,
p .slack-link svg {
  width: 18px;
  height: 18px;
  margin-left: 4px;
  margin-bottom: -3px;
}